export default{
  data() {
    return{
      topIndex: 0,                                       // 顶部导航默认选中的
      topList: ['分类'],                                 // 顶部导航选项
      list: [],                                         // 侧边菜单
      category_id: '',                                  // 侧边栏选中分类ID
      itemList: [],                                     // 选中分类的子分类列表
      
      child: [{
        title: '常用分类',
        arr: ['基础护肤', '包包', '面膜', '平板电脑', '粉底液', '板鞋']
      }, {
        title: '热门分类',
        arr: ['美妆', '空调', '美妆', '空调', '美妆', '空调']
      }],                                  // 主要内容
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#fff',
      swiperList: [{}, {}, {}],           // 轮播图  
      album: {
        imgUrl: '',
        arr: [{
            imgUrl: '',
            sum: 2095
          }, {
            imgUrl: '',
            sum: 2095
        }]
      },
      brand: 3,   // 品牌
      
    }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  onLoad() {
    // 获取分类列表
    this.getCategoryIndex()
  },
  methods: {
    // 获取分类列表
    getCategoryIndex() {
      let that= this
      that.$http({
        url: that.$api.categoryIndex,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            that.list= res.data.list
            that.category_id= res.data.list[0].category_id
            that.itemList= res.data.list[0].child
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '加载失败，请退出重试',
              icon: 'none'
            })
          }
        }
      })
    },
    goMoreGood(id) {
      uni.navigateTo({
        url: './moreGoods?category_id=' + id
      })
    },
    
    // 选择侧边栏
    clickNav(item) {
      this.category_id = item.category_id
      this.itemList = item.child
    },
    // selectTop(index) {
    //   this.topIndex = index
    // },
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    bindClick(info) {
      console.log(info)
    }
  }
}
